import React, { useRef, useState } from "react";
import { toast } from "react-toastify";

const Contact = ({ language, isDarkMode }) => {
  const form = useRef();
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    subject: "",
    message: ""
  });

  const content = {
    EN: {
      title: "Let's Build Something Great",
      namePlaceholder: "Your name",
      emailPlaceholder: "Your email",
      subjectPlaceholder: "What's this about?",
      messagePlaceholder: "Tell me about your project or idea...",
      sendButton: "Send Message",
      successToast: "Message received! I'll get back to you soon.",
      errorToast: "Something went wrong. Please try again.",
      mapTitle: "Based in Amsterdam"
    },
    NL: {
      title: "Laten We Iets Geweldigs Bouwen",
      namePlaceholder: "Uw naam",
      emailPlaceholder: "Uw e-mail",
      subjectPlaceholder: "Waar gaat het over?",
      messagePlaceholder: "Vertel me over uw project of idee...",
      sendButton: "Verstuur Bericht",
      successToast: "Bericht ontvangen! Ik neem snel contact op.",
      errorToast: "Er ging iets mis. Probeer het opnieuw.",
      mapTitle: "Gevestigd in Amsterdam"
    }
  };

  const currentContent = content[language];

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const resetForm = () => {
    setFormData({
      name: "",
      email: "",
      subject: "",
      message: ""
    });
    form.current.reset();
  };

  const sendEmail = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      const response = await fetch("https://formspree.io/f/mqaqryyn", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        resetForm();
        toast.success(currentContent.successToast, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          theme: "colored",
        });
      } else {
        throw new Error("Form submission failed");
      }
    } catch (error) {
      console.error("Form submission error:", error);
      toast.error(currentContent.errorToast, {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        theme: "colored",
      });
    } finally {
      setTimeout(() => {
        setLoading(false);
      }, 1000);
    }
  };

  return (
    <section
      id="contact"
      className={`py-16 px-4 sm:px-6 lg:px-8 transition-colors duration-300 ${isDarkMode ? 'bg-dark-surface' : 'bg-gradient-to-br from-gray-50 to-gray-100'}`}
    >
      <div className="max-w-7xl mx-auto">
        {/* Section Title */}
        <div className="text-center mb-16">
          <h2 className={`text-3xl md:text-4xl lg:text-5xl font-bold mb-4 ${isDarkMode ? 'text-white' : 'text-headingColor'}`}>
            {currentContent.title}
          </h2>
          <div className="w-24 h-1 bg-primaryColor mx-auto rounded-full"></div>
        </div>

        {/* Main Content */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 lg:gap-12 items-start">
          {/* Map Section */}
          <div className="order-2 lg:order-1">
            <div className={`rounded-2xl shadow-xl overflow-hidden ${isDarkMode ? 'bg-dark-card' : 'bg-white'}`}>
              <div className={`p-6 border-b ${isDarkMode ? 'border-dark-border' : 'border-gray-100'}`}>
                <h3 className={`text-xl font-semibold mb-2 ${isDarkMode ? 'text-white' : 'text-headingColor'}`}>
                  {currentContent.mapTitle}
                </h3>
                <p className={isDarkMode ? 'text-gray-400' : 'text-smallTextColor'}>
                  Amsterdam-Zuid, Netherlands
                </p>
              </div>
              <div className="relative h-80 sm:h-96">
                <iframe
                  title="google-maps"
                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d19500.474268381622!2d4.848801944608884!3d52.342140875020675!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x47c609fd919e5b05%3A0xfadb36a3b89991d8!2sAmsterdam-Zuid%2C%20Amsterdam!5e0!3m2!1sen!2snl!4v1680872096824!5m2!1sen!2snl"
                  className="w-full h-full border-0"
                  allowFullScreen=""
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                />
              </div>
            </div>
          </div>

          {/* Contact Form */}
          <div className="order-1 lg:order-2">
            <div className={`rounded-2xl shadow-xl p-6 sm:p-8 ${isDarkMode ? 'bg-dark-card' : 'bg-white'}`}>
              <form ref={form} onSubmit={sendEmail} className="space-y-6">
                {/* Name Input */}
                <div>
                  <label htmlFor="name" className="sr-only">
                    {currentContent.namePlaceholder}
                  </label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleInputChange}
                    required
                    placeholder={currentContent.namePlaceholder}
                    className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-primaryColor focus:border-transparent transition-all duration-200 ${
                      isDarkMode
                        ? 'bg-dark-border border-dark-border text-white placeholder-gray-500 focus:bg-dark-surface'
                        : 'bg-gray-50 border-gray-200 focus:bg-white'
                    }`}
                  />
                </div>

                {/* Email Input */}
                <div>
                  <label htmlFor="email" className="sr-only">
                    {currentContent.emailPlaceholder}
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    required
                    placeholder={currentContent.emailPlaceholder}
                    className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-primaryColor focus:border-transparent transition-all duration-200 ${
                      isDarkMode
                        ? 'bg-dark-border border-dark-border text-white placeholder-gray-500 focus:bg-dark-surface'
                        : 'bg-gray-50 border-gray-200 focus:bg-white'
                    }`}
                  />
                </div>

                {/* Subject Input */}
                <div>
                  <label htmlFor="subject" className="sr-only">
                    {currentContent.subjectPlaceholder}
                  </label>
                  <input
                    type="text"
                    id="subject"
                    name="subject"
                    value={formData.subject}
                    onChange={handleInputChange}
                    required
                    placeholder={currentContent.subjectPlaceholder}
                    className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-primaryColor focus:border-transparent transition-all duration-200 ${
                      isDarkMode
                        ? 'bg-dark-border border-dark-border text-white placeholder-gray-500 focus:bg-dark-surface'
                        : 'bg-gray-50 border-gray-200 focus:bg-white'
                    }`}
                  />
                </div>

                {/* Message Textarea */}
                <div>
                  <label htmlFor="message" className="sr-only">
                    {currentContent.messagePlaceholder}
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleInputChange}
                    required
                    rows={5}
                    placeholder={currentContent.messagePlaceholder}
                    className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-primaryColor focus:border-transparent transition-all duration-200 resize-none ${
                      isDarkMode
                        ? 'bg-dark-border border-dark-border text-white placeholder-gray-500 focus:bg-dark-surface'
                        : 'bg-gray-50 border-gray-200 focus:bg-white'
                    }`}
                  />
                </div>

                {/* Submit Button */}
                <button
                  type="submit"
                  disabled={loading}
                  className="w-full bg-primaryColor text-white font-semibold py-4 px-6 rounded-xl hover:bg-opacity-90 focus:ring-4 focus:ring-primaryColor focus:ring-opacity-50 transition-all duration-200 disabled:opacity-70 disabled:cursor-not-allowed flex items-center justify-center gap-2"
                >
                  {loading ? (
                    <>
                      <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                      <span>{language === "EN" ? "Sending..." : "Versturen..."}</span>
                    </>
                  ) : (
                    <>
                      <i className="ri-send-plane-line text-lg"></i>
                      {currentContent.sendButton}
                    </>
                  )}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;